___CSS_LOADER_EXPORT___.push([module.id, ".bulle-container[data-v-5dd51490]{background-color:#fff;border-radius:8px;box-shadow:0 2px 15px #d3d3d3;padding:20px}.projets-container[data-v-5dd51490]{display:flex;flex-wrap:wrap;gap:10px;margin-top:15px}.projet-button[data-v-5dd51490]:hover{background-color:#4caf50;color:#fff;transform:scale(1.05)}.no-projects[data-v-5dd51490]{color:red;font-size:1rem;margin-top:15px}.projet-button[data-v-5dd51490]{background-color:#e3f2fd;border:1px solid #4caf50;border-radius:5px;color:#06c;cursor:pointer;font-size:1rem;padding:10px 20px;text-align:center;text-decoration:none;transition:background-color .3s,transform .2s}", "",{"version":3,"sources":["webpack://./resources/js/components/layouts/TemplateCompetence.vue"],"names":[],"mappings":"AAyEA,kCACA,qBAAA,CAGA,iBAAA,CAFA,6BAAA,CACA,YAEA,CAEA,oCACA,YAAA,CACA,cAAA,CACA,QAAA,CACA,eACA,CAeA,sCACA,wBAAA,CACA,UAAA,CACA,qBACA,CAEA,8BAGA,SAAA,CADA,cAAA,CADA,eAGA,CACA,gCAIA,wBAAA,CAFA,wBAAA,CACA,iBAAA,CAEA,UAAA,CAEA,cAAA,CADA,cAAA,CALA,iBAAA,CAQA,iBAAA,CACA,oBAAA,CAFA,6CAGA","sourcesContent":["<template>\n  <div v-if=\"competence\" class=\"container-fluid mt-3\">\n    <h1>{{ competence.titre }}</h1>\n    <p>Description de la compétence :</p>\n    <div class=\"mb-2 bulle-container\">\n      <p v-html=\"competence.description_competence\"></p>\n    </div>\n\n    <h2 class=\"mt-4\">Projets Associés :</h2>\n    <div class=\"projets-container\">\n      <a v-for=\"(projet, index) in projetsLiees\" :key=\"index\" class=\"projet-button\"\n        :href=\"`/fiche_projet/${projet.id}`\">\n        {{ projet.name }}\n      </a>\n    </div>\n  </div>\n\n  <div v-else>\n    <p>Chargement...</p>\n  </div>\n</template>\n\n<script>\nimport axios from \"axios\";\n\nexport default {\n  props: {\n    competence: {\n      type: Object,\n      required: true,\n    },\n  },\n  data() {\n    return {\n      projets: [], // Liste des projets récupérés\n    };\n  },\n  computed: {\n    projetsLiees() {\n      // Vérifie si project_id est un tableau ou une chaîne nécessitant un traitement\n      const projetIds = Array.isArray(this.competence.projet_id)\n        ? this.competence.projet_id\n        : String(this.competence.projet_id)\n          .replace(/[{}]/g, \"\") // Supprimer les accolades\n          .split(\",\") // Diviser en tableau\n          .map((id) => parseInt(id.trim(), 10)) // Convertir en nombres\n          .filter((id) => !isNaN(id)); // Retirer les valeurs invalides\n\n      return projetIds\n        .map((id) =>\n          this.projets.find((projet) => projet.id === id)\n        )\n        .filter((projet) => projet); // Exclure les compétences non trouvées\n    },\n  },\n  methods: {\n    async fetchProjets() {\n      try {\n        const response = await axios.get(\"/gestion/projets/get\");\n        this.projets = response.data;\n        console.log(\"Projets récupérés depuis l'API :\", this.projets);\n      } catch (error) {\n        console.error(\"Erreur lors de la récupération des projets :\", error);\n      }\n    },\n  },\n  async mounted() {\n    await this.fetchProjets();\n  },\n};\n</script>\n\n<style scoped>\n.bulle-container {\n  background-color: #fff;\n  box-shadow: 0px 2px 15px lightgray;\n  padding: 20px;\n  border-radius: 8px;\n}\n\n.projets-container {\n  display: flex;\n  flex-wrap: wrap;\n  gap: 10px;\n  margin-top: 15px;\n}\n\n.projet-button {\n  padding: 10px 20px;\n  border: 1px solid #4caf50;\n  border-radius: 5px;\n  background-color: #e3f2fd;\n  color: #0066cc;\n  font-size: 1rem;\n  cursor: pointer;\n  transition: background-color 0.3s, transform 0.2s;\n  text-align: center;\n  text-decoration: none;\n}\n\n.projet-button:hover {\n  background-color: #4caf50;\n  color: #fff;\n  transform: scale(1.05);\n}\n\n.no-projects {\n  margin-top: 15px;\n  font-size: 1rem;\n  color: #ff0000;\n}\n.projet-button {\n  padding: 10px 20px;\n  border: 1px solid #4caf50;\n  border-radius: 5px;\n  background-color: #e3f2fd;\n  color: #0066cc;\n  font-size: 1rem;\n  cursor: pointer;\n  transition: background-color 0.3s, transform 0.2s;\n  text-align: center;\n  text-decoration: none;\n}\n</style>\n"],"sourceRoot":""}]);
